#home{
  position: relative;
  height: 98.7vh;
}
#home video {
  object-fit: cover;
}
#home .video_backdrop {
  background: rgba(46, 46, 46, 0.548);
  position: absolute;
  height: 100%;
  width: 100%;
  z-index: 2;
}
#home .video_backdrop .circle{
  cursor: pointer;
  position: absolute;
  width: 70px ;
  height: 70px ;
  z-index: 3;
  border: 2px solid #ffff;
  border-radius: 50px;
  top: 50%;
  left: 50%;
  display: flexbox;
  align-items: center;
  justify-content: center;
}
#home .video_backdrop .circle:hover{
  opacity: .7;
}
#home .video_backdrop .triangle{
  width: 33px;
  height: 2px;
  background: #ffff;
  position: absolute;
  top: 50%;
  right: 40%;
  transform: rotate(-90deg);
}

#home .video_backdrop .triangle::after,
#home .video_backdrop .triangle::before{
  content: "";
  width: 33px;
  position: absolute;
  
  height: 2px;
  background: #ffff;
}
#home .video_backdrop .triangle::after{
  transform: rotate(60deg);
  left: -8px;
  top: 14px;
  
}
#home .video_backdrop .triangle::before{
  transform: rotate(-60deg);
  left: 8px;
  top: 14px;
}


#home {
  .video_backdrop {
    color: #fff;
  }
}
